.container {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    padding-bottom: 30px;
    padding-top: 10px;
    width: 100%;
}

.container2 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #FFFFFF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    margin-bottom: 15px;
}

.scrollContainer {
    display: flex;
    flex-direction: row;
    height: 330px;
    overflow-x: auto;
    scroll-behavior: smooth;
    padding: 10px;
}

.experienceContainer {
    width: 350px;
    height: 300px;
    position: relative;
    margin-bottom: 20px;
    margin-right: 20px;
    flex-shrink: 0;
}

.projectContainer {
    padding: 15px;
    width: 500px;
    height: 330px;
    margin: 10px;
    transition: transform 0.3s ease;
}

.projectContainer:hover {
    transform: scale(1.05);
}

.skillContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 15px;
    transition: transform 0.3s ease;
}

.skillContainer:hover {
    transform: scale(1.05);
}

.titleContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.content,
.hoverContent {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.experienceContainer img {
    width: 50%;
    height: auto;
    margin: 7px;
    border-radius: 1;
}

.navyText {
    color: #050d41;
    font-family: 'Lexend', sans-serif;
    text-align: center;
    font-size: 17px;
}

.skillImg {
    max-width: 200px;
    max-height: 100px;
    width: auto;
    height: auto;
    border: grey 1px solid;
}

.projImg {
    max-width: 17500px;
    max-height: 80px;
    width: auto;
    height: auto;
}

#skills {
    background-color: rgb(225, 234, 255);
    border-top: navy 1px solid;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.3);
}

h3 {
    font-size: 14px;
}

p {
    font-size: 12px;
}

@media (max-width: 768px) {
    .container {
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .experienceContainer {
        width: 80%;
        margin-bottom: 15px;
        margin-left: 15px;
    }

    .projectContainer {
        width: 90%;
    }
}